<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="paintingTookDialog"
        >
            <template>
                <v-card class="ip_add">

                    <div class="ip_add__title">
                        <span class="text-black">Mahsulot olinmoqda</span>
                        <v-btn icon @click="closeDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOutsourcePainting" v-slot="{ handleSubmit, invalid }">
                        <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>


                            <div>
                                <span class="text-black ml-1">Massa (kg)</span>

                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                                 v-slot="{ errors }">
                                    <v-text-field :error-messages="errors" 
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(product.amount, 'amount')"
                                        v-model="product.amount" min="1"
                                        outlined placeholder="Misol: 1,200 kg" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Isporeniya</span>

                                <ValidationProvider :rules="{required: true}"
                                v-slot="{ errors }" class="d-flex align-top ">
                                    <v-text-field :error-messages="errors" 
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        v-model="product.evaporation" min="1" max="100"
                                        outlined suffix="%" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Berilgan summa</span>
                                <ValidationProvider :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}"
                                v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(product.paid, 'paid')"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        outlined min="1"
                                        placeholder="Misol: 1,000,000 so’m"
                                        class="mt-2" v-model="product.paid">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    class="backBtn btn"
                                    elevation="0"
                                    @click="closeDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn btn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['paintingTookDialog', 'item'],
    mixins: [dateMixin, loaderMixin],
    data(){
        return{
            // ============= SECONDARY_DATA =============
            product: {
                amount: 0,
                evaporation: '',
                paid: 0
            },
            item2: '',
            // ============= SECONDARY_DATA =============


            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= CLEAR_ERRORS START =============
        'product': {
            handler(newValue){
                if(newValue){
                    this.clearErrors()
                }
            }, deep: true
        },
        'item': {
            handler(newValue){
                if(newValue){
                    this.item2 = newValue
                }
            }, deep: true
        },
        // ============= CLEAR_ERRORS END =============





        // ============= OTHER_WATCHERS START =============
        'product.amount':{
            handler(newValue, oldValue){
                if(newValue){
                    let a = newValue.toString().replace(/,/g, '') * 100
                    this.product.evaporation = a / this.item2.amount
                    this.product.evaporation = (100 - this.product.evaporation).toFixed(3);
                    console.log(this.product.evaporation)
                }
            }, deep: true
        },
        'product.evaporation':{
            handler(newValue, oldValue){
                if(newValue > 100){
                    this.$toast.error('Isporeniya 100% dan oshmasligi zarur!')
                }
            }, deep: true
        },
        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){
        await this.refresh()
    },
    computed: {
        ...mapState(
            {
                factory_threads: state => state.outsource.factory_threads,
                orders: state => state.outsource.orders
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                postPaintingProductTook: 'outsource/postPaintingProductTook',
                factoryThreads: 'outsource/factoryThreads',
                fetchOrders: 'outsource/fetchOrders'
            }
        ),





        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                console.log(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);
                console.log(currency)

                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'paid'){
                    this.product.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============









        // ============= REFRESH START =============
        async refresh(){
            await this.fetchOrders({type: 'material', factory: this.$route.params.id, status: 'in_process'})
            this.existingMatos = this.orders.data;
        },
        // ============= REFRESH START =============











        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                amount: 0,
                evaporation: '',
                paid: 0
            },

            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.$parent.paintingTookDialog = false;
            this.$refs.formOutsourcePainting.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============
        
        
        
        
        
        
        
        
        
   

        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formOutsourcePainting.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    // ============= ADDING_NEW_ORDER START =============
                    let product = await this.postPaintingProductTook(
                    {
                        created_at: this.date,
                        order_id: this.item2.id,
                        product_id: parseInt(this.item2.product.id),
                        type: 'painted',
                        amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                        paid: parseFloat(this.product.paid.toString().replace(/,/g, '')),
                        percentage: this.product.evaporation,
                    })
                    if(product.response && product.response.status == 422){
                        if(product.response.data.errors.amount){
                            this.errorMessage = product.response.data.errors.amount[0];
                            this.errorPlace = 'amount';
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(product.response && product.response.status == 409){
                        if(product.response.data.message){
                            this.errorMessage = product.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli mahsulot olindi!');
                        this.$toast.success('Buyurtmangiz yopildi!');
                        this.closeDialog();
                        this.$store.commit('main/SET_UPDATE_OUTSOURCE_PAINTING_CURRENT', true)
                        this.$store.commit('main/SET_UPDATE_OUTSOURCE_PAINTING_OUTCOME', true)

                    }
                    // ============= ADDING_NEW_ORDER END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============



    }

}
</script>


<style>

</style>
