                    <img src="@/assets/icons/box-tick-green.svg" alt="box-tick"
                    style="width: 20px; margin-right: 3px;">
                    <span style="color: #43D9A9;">Mahsulot olish</span>
                </div>
            </template>

            <!-- <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'outsource_boyoqxona_jarayon')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'outsource_boyoqxona_jarayon')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template> -->

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>
            
        </v-data-table>

        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>



        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditIncomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <!-- <div class="mt-4">
                            <span class="text-black ml-1">Turni tanlang</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'code']}"
                              v-slot="{ errors }">
                                <v-select :items="filteredItems" v-model="product.product_category_id" 
                                    class="mt-2" item-value="id" label="Tanlang"
                                    outlined :error-messages="errors" item-text="name">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Nomni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.name" class="mt-2"
                                    placeholder="Misol: Vilvit" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Kodni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.code" type="number" class="mt-2"
                                    placeholder="Misol: 666777" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Tavsif kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'description']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.description" class="mt-2"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div> -->

                            
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
        <PaintingProductTook :paintingTookDialog="paintingTookDialog" :item="item"></PaintingProductTook>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import PaintingProductTook from '@/components/modals/Outsource/PaintingProductTook'
import DeleteModal from '@/components/modals/DeleteModal.vue'


export default {
    mixins: [priceMixin, loaderMixin],
    components:{
        PaintingProductTook,
        DeleteModal,
        NoData
    },
    data(){
        return {
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            paintingTookDialog: false,
            headers: [
                {text: 'Partiya', value: 'batch', align: 'start', class: 'table-header'},
                {text: 'Berildi', value: 'given', align: 'center', class: 'table-header'},
                {text: 'Mahsulot kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'title', align: 'center', class: 'table-header'},
                {text: 'Massa', value: 'amount', align: 'center', class: 'table-header', align: 'center', class: 'table-header'},
                {text: 'Buyurtma rang', value: 'color_code', align: 'center', class: 'table-header'},
                {text: 'Buyurtma sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'took_product', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            item: '',
            // ============= SECONDARY_DATA =============


            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= CLEAR_ERRORS START =============
        '$store.state.main.outsource_painting_current': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_PAINTING_CURRENT', false)
                }
            },
            deep: true,
            immediate: true
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        // ============= CLEAR_ERRORS END =============




        // ============= OTHER_WATCHERS START =============

        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){
        await this.refresh()

        if(!this.$can('read', 'outsource_boyoqxona_mahsulot_olindi')){
            this.headers.pop();
        }
    },
    computed: {
        ...mapState(
            {
                orders: state => state.outsource.orders,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                fetchOrders: 'outsource/fetchOrders'
            }
        ),
        // ============= REFRESH START =============
        async refresh(){
            this.loading = true
            await this.fetchOrders({page: this.page, type: 'painting', factory: this.$route.params.id, status: 'in_process',
            search: this.searchValue, date: this.dateFilterValue})
            this.items = this.orders.data;

            this.page = this.orders.current_page;
            this.pageCount = Math.ceil(this.orders.total / 10);

            this.items.forEach((element) => {
                element.batch = element.code
                element.given = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.amount = element.amount
                element.color_code = element.color_code

                if(element.created_at)
                element.date = this.$dayjs(element.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        // ============= REFRESH START =============
        async openProductTook(item){
            this.paintingTookDialog = true
            this.item = await 0
            this.item = await item
        },
        closeEditModal(){
            this.editModal = false
        },




    }

}
</script>

