<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
            <template v-slot:item.batch="{item}">
                <div class="d-flex">
                    <span class="text-black">#{{ item.batch }}</span>
                </div>
            </template>

            <template v-slot:item.given="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.given }}</span>
                </div>
            </template>

            <template v-slot:item.code="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>

            <template v-slot:item.name="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>

            <template v-slot:item.amount="{item, index}">
                <v-menu 
                    v-model="menu[index]"
                    :close-on-content-click="false"
                    :nudge-width="220"
                    offset-x
                    offset-y>
                    <template v-slot:activator="{ on, attrs }">
                        <a class="d-flex justify-center" v-bind="attrs" v-on="on">
                            <span style="color: #2782F6;">{{ formatPrice(item.amount) }} kg</span>
                        </a>
                    </template>
                    <v-list class="process">
                        <div class="process-title">
                            <span>Qo'shimcha ma'lumotlar</span>
                        </div>
                        <div class="pa-1">Isporeniya massasi: {{calculateEvaporation(item)}} kg</div>
                        <div class="pa-1">Isporeniya (%): {{item.percentage}}%</div>
                    </v-list>
                </v-menu>
            </template>

            <template v-slot:item.color_code="{item}">
                <div class="d-flex justify-center">
                    <!-- <div class="small_circle" :style="{background: item.color_code}"></div> -->
                    <span class="text-black">{{ item.color_code }}</span>
                </div>
            </template>

           

            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <!-- <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'outsource_boyoqxona_chiqim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'outsource_boyoqxona_chiqim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template> -->

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>
            
        </v-data-table>

        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>

    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
// import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
// import DeleteModal from '@/components/modals/DeleteModal.vue'


export default {
    mixins: [priceMixin],
    components:{
        NoData
        // DeleteModal,
    },
    data(){
        return {
            menu: [],
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Partiya', value: 'batch', align: 'start', class: 'table-header'},
                {text: 'Berildi', value: 'given', align: 'center', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Massa', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Buyurtma rang', value: 'color_code', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.outsource_painting_outcome': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_PAINTING_OUTCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
    },
    computed: {
        ...mapState({
            ready_raw: state => state.supply.ready_raw,
            searchValue: state => state.main.searchValue,
            dateFilterValue: state => state.main.dateFilterValue
            // item: state => state.outsource.item
        }),
        
    },
    methods: {
        ...mapActions({
            fetchReadyRaw: 'supply/fetchReadyRaw'
        }),
        calculateEvaporation(item){
            let percentage2 = 100 - item.percentage

            let hundredP = (item.amount * 100) / percentage2
            
            let amount2 = hundredP - item.amount

            return amount2
        },
        async refresh(){
            this.loading = true
            await this.fetchReadyRaw({factory: this.$route.params.id, type: 'painting', 
            parent_id: 'mato_va_yoqa', search: this.searchValue, date: this.dateFilterValue, page: this.page})
            this.page = this.ready_raw.current_page;
            this.pageCount = Math.ceil(this.ready_raw.total / 10);
            
            this.items = this.ready_raw.data;
            this.items.forEach((elem) => {
                elem.batch = elem.order.code
                elem.given = elem.order.product.product_category.name
                elem.code = elem.order.product.code
                elem.name = elem.order.product.name
                elem.color_code = elem.order.color_code
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

