<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->

            <!-- <template v-slot:item.status="{item}">
                <div class="d-flex">
                    <img src="@/assets/images/berildi.png" alt="">
                </div>
            </template> -->

            <template v-slot:item.batch="{item}">
                <div class="d-flex">
                    <span class="text-black">#{{ item.batch }}</span>
                </div>
            </template>

            <template v-slot:item.given="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.given }}</span>
                </div>
            </template>

            <template v-slot:item.code="{item}">
                <div class="d-flex">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>

            <template v-slot:item.title="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.title }}</span>
                </div>
            </template>

            <template v-slot:item.amount="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ formatPrice(item.amount) }} kg</span>
                </div>
            </template>

            <template v-slot:item.color_code="{item}">
                <div class="d-flex">
                    <!-- <div class="small_circle" :style="{background: item.color_code}"></div> -->
                    <span class="text-black">{{ item.color_code }}</span>
                </div>
            </template>

            <template v-slot:item.price="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ formatPrice(item.price) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.sum="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ formatPrice(item.sum) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.date="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" 
                    @click="(product = item.order) && (editModal = true )"
                    v-if="$can('update', 'outsource_boyoqxona_kirim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" 
                    @click="(element = item.order) && (deleteModal = true )"
                    v-if="$can('delete', 'outsource_boyoqxona_kirim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template>

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>
    

        </v-data-table>
        
        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>



        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditOutcomeMaterial" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <div class="mt-5">
                            <span class="text-black ml-1">Partiya raqami</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'code']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.code"  class="mt-2"
                                    placeholder="Misol: 400100" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">Rang kodi</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'color_code']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.color_code"  class="mt-2"
                                    placeholder="Misol: FFFFFF" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">Massa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.amount)"  class="mt-2"
                                    @input="formatPrice2($event, 'amount')"
                                    placeholder="Misol: 10 kg" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">KG narxi</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'price']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.price)"  class="mt-2"
                                    @input="formatPrice2($event, 'price')"
                                    placeholder="Misol: 10,000 so`m" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">Umumiy summa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                              v-slot="{ errors }">
                                <v-text-field outlined  class="mt-2"
                                    @input="formatPrice2($event, 'total_price')"
                                    :value="getTotalPrice"
                                    placeholder="Misol: 100,000 so`m" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>


        <DeleteModal 
        :deleteModal="deleteModal" 
        :user_id="element.id" 
        :actionName="'warehouse/deleteOrder'"
        :modalTitle="'Mahsulot o‘chirilishi'"
        :modalDesc="'Bu mahsulot o‘chirmoqchimisiz?'"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'


export default {
    mixins: [priceMixin, loaderMixin],
    components:{
        DeleteModal,
        NoData
    },
    data(){
        return {
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            headers: [
                // {text: 'Status', value: 'status'},
                {text: 'Partiya', value: 'batch', align: 'start', class: 'table-header'},
                {text: 'Berildi', value: 'given', align: 'center', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'title', align: 'center', class: 'table-header'},
                {text: 'Massa', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Buyurtma rang', value: 'color_code', align: 'center', class: 'table-header'},
                {text: 'Buyurtma KG summa', value: 'price', align: 'center', class: 'table-header'},
                {text: 'Buyurtma summa', value: 'sum', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            element: {},
            product: {
                code: '',
                amount: '',
                color_code: '',
                price: '',
                total_price: '',
            },
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh();

        if(!this.$can('update', 'outsource_boyoqxona_kirim') && !this.$can('delete', 'outsource_boyoqxona_kirim')){
            this.headers.pop();
        }
    },
    watch: {
        '$store.state.main.outsource_painting_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_PAINTING_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
    },
    computed: {
        ...mapState({
            material_outcome: state => state.warehouse.material_outcome,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        }),
        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.product.price.toString().replace(/,/g, '')
            let amount = this.product.amount.toString().replace(/,/g, '');

            let result = price * amount
            
            return  this.product.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods: {
        ...mapActions({
            fetchMaterialOutcome: 'warehouse/fetchMaterialOutcome',
            updateOrder: 'warehouse/updateOrder'
        }),
        async refresh() {
            this.loading = true
            await this.fetchMaterialOutcome({page: this.page, factory: this.$route.params.id, 
            parent_id: 'mato_va_yoqa', search: this.searchValue, date: this.dateFilterValue})
            this.items = this.material_outcome.data


            this.page = this.material_outcome.current_page;
            this.pageCount = Math.ceil(this.material_outcome.total / 10);

            this.items.forEach((element) => {
                element.batch = element.order.code
                element.given = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.amount = element.order.amount
                element.color_code = element.order.color_code
                element.price = element.order.price
                element.sum = element.order.total_price

                if(element.created_at)
                element.date = this.$dayjs(element.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },





        // ============= PRICE_FORMATTER START =============
        formatPrice2(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'price'){
                    this.product.price = currency
                }else if(input == 'total_price'){
                    this.product.total_price = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                code: '',
                amount: '',
                color_code: '',
                price: '',
                total_price: '',
            }
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditDialog(){
            this.clearInputs();
            this.editModal = false;
            this.$refs.formEditOutcomeMaterial.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditOutcomeMaterial.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= EDITING_IP START =============
                    let abc = await this.updateOrder(
                        { 
                            id: this.product.id,
                            factory_product_case: 'painted',
                            storage_product_case: 'raw',
                            storage_type_id: '1',
                            code: this.product.code,
                            amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                            color_code: this.product.color_code,
                            price: parseFloat(this.product.price.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.product.total_price.toString().replace(/,/g, '')),
                        })

                    if(abc.response && abc.response.status == 422){
                        if(abc.response.data.errors.amount){
                            this.errorMessage = abc.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(abc.response.data.errors.price){
                            this.errorMessage = abc.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(abc.response.data.errors.total_price){
                            this.errorMessage = abc.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(abc.response && abc.response.status == 409){
                        if(abc.response.data.message){
                            this.errorMessage = abc.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeEditDialog();
                        this.refresh()
                        // this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }
                    // ============= EDITING_IP END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        }
        // ============= ON_SUBMIT END =============


    }
}
</script>

<style>

</style>